@import "../../styles/variables.scss";

.header {
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  background-color: $headerBackgroundColor;
  z-index: 1000;
}

.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: $desktop;
  padding: 20px;
  border-bottom: 1px solid $lightColor;
}

.container {
  display: flex;
  align-items: center;
}

.link {
  position: relative;
  font-weight: 600;
  font-size: 18px;
  line-height: 110%;
  text-decoration: none;
  color: $lightColor;
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  // padding-bottom: 5px;

  &:hover {
    color: $activeLinkColor;
  }
  & + & {
    margin-left: 30px;
  }
}

.active {
  color: $activeLinkColor;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -20px;
    display: block;
    width: 100%;
    height: 5px;
    background-color: $activeLinkColor;
    border-radius: 2px;
  }
}

