.activeLink {
  color: $hoverOrangeColor;

  &::after {
    content: "";
    display: inline-block;
    width: 100%;
    height: 1px;
    background: $hoverOrangeColor;
    position: absolute;
    bottom: 4px;
    transition: 0.3s;
    opacity: 1;
    left: 0px;
    bottom: 0px;
  }
}
